@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onDesktop {
  @media (min-width: 1280px) {
    @content;

  }
}

@mixin onTablet {
  @media (min-width: 768px) {
    @content;

  }
}


@mixin onPhone {
  @media (min-width: 320px) {
    @content;

  }
}

@mixin onPhoneMax {
  @media (max-width: 400px) {
    @content;

  }
}
